import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
export interface shifts {
  bar : string;
  bartender_1: string;
  bartender_2: string;
  bartender_3: string;
  bartender_4: string;
  bartender_5: string;
  shift_start: string;
  shift_end: string;
  shift_type: string;
}
@Injectable({
  providedIn: 'root'
})
export class ShiftsService {

  constructor(public http: HttpClient) { }
  getShifts(){
    return this.http.get<shifts[]>('/api/shifts');
  }

}
