import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

export interface BeerLocation {
  bar: string;
  price: number;
  customers: number;
  beer: string;
}

@Injectable({
  providedIn: 'root'
})
export class BeersService {

  constructor(private http: HttpClient) { }
  getBeers() {
    return this.http.get<any[]>('/api/beers');
  }
  getBarsSelling(beer: string) {
    return this.http.get<BeerLocation[]>('/api/bars-selling/${beer}');
  }
  getBeerLikes() {
    return this.http.get<any[]>('/api/beerlikes');
  }
  getBeerBrewery(beer?: string): any {
    if (beer) {
      return this.http.get<string>('/api/beer-brewery/${beer}');
    }
    return this.http.get<string[]>('/api/beer-brewery');
  }
}
