import { Component, OnInit } from '@angular/core';
import {BeersService} from '../beers.service'
declare const Highcharts: any;

@Component({
  selector: 'app-beerlikes',
  templateUrl: './beerlikes.component.html',
  styleUrls: ['./beerlikes.component.css']
})
export class BeerlikesComponent implements OnInit {

  constructor(private beerService: BeersService) {
    this.beerService.getBeerLikes().subscribe(
      data => {
      console.log(data);
      const beers = [];
      const counts = [];
      data.forEach(beer => {
      beers.push(beer.beer);
      counts.push(beer.likescount);
      });
      //use the line below to render the graph
      this.renderChart(beers, counts);
      }
      );

   }
   renderChart(beers: string[], counts: number[]) {
    Highcharts.chart('bargraph', {
    chart: {
    type: 'column'
    },
    title: {
    text: 'Beers Liked By Customers'
    },
    xAxis: {
    categories: beers,
    title: {text: 'Beer'
  }
  },
  yAxis: {
  min: 0,
  title: {
  text: 'Number of customers'
  },
  labels: {
  overflow: 'justify'
  }
  },
  plotOptions: {
  bar: {
  dataLabels: {
  enabled: true
  }
  }
  },
  legend: {
  enabled: false
  },
  credits: {
  enabled: false
  },
  series: [{
  data: counts
  }]
  });
  }   
  ngOnInit() {
  }

}
