import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WelcomeComponent } from './welcome/welcome.component';
import { BarDetailsComponent } from './bar-details/bar-details.component';
import { BeerDetailsComponent } from './beer-details/beer-details.component';
import { BeerslistComponent } from './beerslist/beerslist.component';
import { BarslistComponent } from './barslist/barslist.component';
import { DrinkerslistComponent } from './drinkerslist/drinkerslist.component';
import { TransactionsComponent } from './transactions/transactions.component';
import { ShiftsComponent } from './shifts/shifts.component';
import { InsightsComponent } from './insights/insights.component';
import { BeerlikesComponent } from './beerlikes/beerlikes.component';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    component: WelcomeComponent
},
{
  path: 'static',
  pathMatch: 'full',
  redirectTo: 'bars'
  },
{
  path: 'bars/:bar',
 pathMatch: 'full',
 component: BarDetailsComponent
},
{
  path: 'beerlikes',
 pathMatch: 'full',
 component: BeerlikesComponent
},
{
  path: 'shifts',
 pathMatch: 'full',
 component: ShiftsComponent
},
{
  path: 'bars',
 pathMatch: 'full',
 component: BarslistComponent
},
{
  path: 'transactions',
 pathMatch: 'full',
 component: TransactionsComponent
},
{
  path: 'drinkers',
 pathMatch: 'full',
 component: DrinkerslistComponent
},
{
  path: 'beers/:beer',
  pathMatch: 'full',
  component: BeerDetailsComponent
},
{
  path: 'beers',
  pathMatch: 'full',
  component: BeerslistComponent
},
{
  path: 'insights',
  pathMatch: 'full',
  component: InsightsComponent
}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
